.header { position: relative; z-index: 0; padding: 3.96825% 0 70px; background: #fff000; color: #000; }
.header:before { content: ""; position: absolute; top: 0; left: 0; right: 0; bottom: 0; background: #fff000; z-index: -1; }
.header .logo { position: absolute; top: 50%; left: 10px; margin-top: -0.5em; }

.header__row { position: relative; max-width: 1260px; padding: 0 10px; margin: auto; text-align: right; }

.header__triangles { position: absolute; z-index: -2; top: 100%; left: 50%; margin: -65px 0 0 -227px; }

.logo { display: inline-block; position: relative; color: #000; font-size: 40px; line-height: 1; text-transform: uppercase; text-decoration: none; font-family: "Politica", Helvetica, Arial, sans-serif; font-weight: normal; letter-spacing: -0.01em; }
.logo:before { content: ""; position: absolute; top: 100%; left: 2px; right: 0; height: 5px; background-image: inline("svg/wave.svg"); background-repeat: repeat-x; }

.say-hello { position: relative; display: inline-block; vertical-align: middle; padding: 3px 6px 2px; border: 2px solid #000; color: inherit; background: #fff000; font-size: 21px; line-height: 1; font-family: "Politica-Bold", Helvetica, Arial, sans-serif; font-weight: normal; letter-spacing: -0.01em; text-transform: uppercase; text-decoration: none; }
.say-hello:before { content: ""; position: absolute; top: 100%; left: 8px; width: 0px; height: 0px; border-style: solid; border-width: 0 10px 10px 0; border-color: transparent #000 transparent transparent; }
.say-hello:after { content: ""; position: absolute; top: 100%; left: 12px; width: 0px; height: 0px; border-style: solid; border-width: 0 4px 5px 0; border-color: transparent #fff000 transparent transparent; }

.menu-btn { position: relative; display: inline-block; vertical-align: middle; width: 30px; height: 30px; margin-left: 48px; background: none; text-decoration: none; color: inherit; }
.menu-btn:hover { color: red; }
.menu-btn:hover .menu-btn__inner { transform: rotate(90deg); background: red; }
.menu-btn:hover .menu-btn__inner:before { transform: rotate(35deg); width: 50%; margin-bottom: 2px; background: red; }
.menu-btn:hover .menu-btn__inner:after { transform: rotate(-35deg); width: 50%; margin-top: 2px; background: red; }

.menu-btn__inner { position: absolute; top: 50%; left: 50%; margin: -1px 0 0 -13px; box-sizing: border-box; width: 26px; height: 2px; background: #000; transition: all 0.2s linear; }
.menu-btn__inner:before, .menu-btn__inner:after { content: ""; position: absolute; right: 0; width: 100%; height: 100%; transition: all 0.2s linear; background: inherit; }
.menu-btn__inner:before { bottom: 100%; margin-bottom: 4px; }
.menu-btn__inner:after { top: 100%; margin-top: 4px; }

.promo-text { position: relative; max-width: 660px; box-sizing: border-box; margin: auto; font-size: 32px; line-height: 1.15; color: inherit; text-align: center; text-transform: uppercase; font-family: "Politica-Bold", Helvetica, Arial, sans-serif; font-weight: normal; letter-spacing: -0.01em; }
.promo-text:before { content: ""; position: absolute; top: 100%; left: 9.09091%; width: 10px; height: 10px; margin-top: 12px; border-radius: 50%; background: #000; }

.promo-text__row { display: inline-block; border: 2px solid; border-width: 2px 0; }
.promo-text__row + .promo-text__row { margin-top: -2px; }

.anim-text-box { margin: 100px 0; font-size: 238px; font-family: "Politica-Bold", Helvetica, Arial, sans-serif; font-weight: normal; letter-spacing: -0.01em; line-height: 1; text-align: center; }

.heading { margin: 30px 0; font-size: 40px; text-transform: uppercase; text-align: center; font-family: "Politica", Helvetica, Arial, sans-serif; font-weight: normal; letter-spacing: -0.01em; }
.heading:first-child { margin-top: 0; }

.heading__inner { display: inline-block; vertical-align: middle; font-weight: normal; position: relative; font-weight: normal; font-size: inherit; }

.heading_wave .heading__inner:before, .heading_wave .heading__inner:after { content: ""; position: absolute; top: 50%; width: 116px; height: 9px; margin-top: -5px; background-image: inline("svg/wave-large.svg"); background-repeat: repeat-x; }
.heading_wave .heading__inner:before { right: 100%; margin-right: 35px; }
.heading_wave .heading__inner:after { left: 100%; margin-left: 35px; }

.heading_wheel .heading__inner { padding: 4px 5px 2px; border: 2px solid; line-height: 34px; }
.heading_wheel .icon-steering { margin: -25px 5px -24px; }

.heading_fish .icon-fish { vertical-align: middle; }
.heading_fish .icon-fish:first-child { margin-right: 45px; }
.heading_fish .icon-fish:last-child { margin-left: 45px; transform: scale(-1, 1); }

.section { padding: 35px 0; position: relative; box-sizing: border-box; min-height: 400px; z-index: 0; }

.section_bg { background: #EDF0F7; }

.figure-group { font-size: 0; margin: 0 -15px; }

.figure { display: inline-block; vertical-align: top; padding: 0 10px; width: 25%; box-sizing: border-box; text-align: center; font-size: 16px; cursor: default; }
.figure:hover .figure__icon .icon-octa { fill: #fff000; stroke: #fff000; transform: scale(1.2); }
.figure:hover .figure__strong { box-shadow: 0 4px 0 -2px #fff000; }

.figure__icon { position: relative; height: 125px; line-height: 125px; margin-bottom: 40px; }
.figure__icon .icon { vertical-align: middle; position: relative; }
.figure__icon .icon-octa { position: absolute; top: 0; left: 50%; margin-left: -0.5em; transition: all 0.2s linear; fill: transparent; }
.figure__icon .icon-diamond { top: 4px; }
.figure__icon .icon-eye { top: -5px; }
.figure__icon .icon-letter { top: 2px; }

.figure__strong { position: relative; display: inline-block; margin-bottom: 20px; font-size: 26px; line-height: 1.38; font-family: "Roboto-Regular", Helvetica, Arial, sans-serif; font-weight: normal; transition: box-shadow 0.2s linear; }

.figure__regular { font-family: "Roboto-Light", Helvetica, Arial, sans-serif; font-weight: normal; letter-spacing: 0.01em; }

.slider { margin-left: 42.70833%; position: relative; }

.slider__nav { position: absolute; bottom: -5px; left: 25px; }

.slider__prev, .slider__next { background: none; text-decoration: none; color: inherit; }
.slider__prev .icon, .slider__next .icon { transition: fill 0.2s linear; }
.slider__prev:hover .icon, .slider__next:hover .icon { fill: #fff000; }

.slider__next { margin-left: 22px; }
.slider__next .icon { transform: scale(-1, -1); }

.feedback { color: #333; *zoom: 1; }
.feedback:after { content: " "; display: table; clear: both; }

.feedback__ava { float: left; width: 128px; height: 128px; overflow: hidden; }
.feedback__ava img { display: block; max-width: 100%; max-height: 100%; }

.feedback__text { margin-left: 165px; }

.feedback__person { display: block; margin-top: 40px; font-style: normal; font-weight: normal; font-size: 13px; }

.feedback__name { display: block; font-family: "Roboto-Bold", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 16px; }

.money-back { position: absolute; top: 40px; width: 310px; padding-bottom: 135px; }

.money-back__text { position: relative; padding: 17px 29px; box-sizing: border-box; background: #EDF0F7; border: 17px solid #FFF; font-size: 28px; font-family: "Roboto-Regular", Helvetica, Arial, sans-serif; font-weight: normal; }
.money-back__text:before, .money-back__text:after { content: ""; position: absolute; top: 100%; right: 0; }
.money-back__text:before { width: 0px; height: 0px; border-style: solid; border-width: 0 64px 50px 0; border-color: transparent #FFF transparent transparent; margin: 17px -17px 0 0; }
.money-back__text:after { width: 0px; height: 0px; border-style: solid; border-width: 0 42px 32px 0; border-color: transparent #EDF0F7 transparent transparent; }

.money-back__icon { position: absolute; bottom: 0; right: 0; margin: 0 -26px 0; }

.logotypes { font-size: 0; text-align: center; }

.logotypes__item { display: inline-block; vertical-align: middle; margin: 20px 30px; }
.logotypes__item img { display: block; }

.form { margin: 55px -10px 0; font-size: 0; }

.form__field { padding: 0 10px; margin-bottom: 20px; box-sizing: border-box; }

.form__field_half { display: inline-block; vertical-align: top; width: 50%; }

.input { display: block; width: 100%; padding: 18px 20px; box-sizing: border-box; background: #FFF; font-family: "Roboto-Bold", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 13px; text-transform: uppercase; line-height: 1.2; color: #333; border-radius: 3px; }
.input:-ms-input-placeholder { color: #333; line-height: normal; }
.input:-moz-placeholder { color: #333; line-height: normal; }
.input::-moz-placeholder { color: #333; line-height: normal; }
.input::-webkit-input-placeholder { color: #333; line-height: normal; }
.input::placeholder { color: #333; line-height: normal; }

.btn { display: block; margin: 40px auto 0; width: 295px; height: 66px; background: #fff000; font-family: "Politica", Helvetica, Arial, sans-serif; font-weight: normal; letter-spacing: -0.01em; font-size: 40px; text-transform: uppercase; }

.contacts { *zoom: 1; }
.contacts:after { content: " "; display: table; clear: both; }

.contacts__left { float: left; }

.contacts__right { float: right; }

.contacts__center { text-align: center; }

.contact { display: inline-block; vertical-align: top; font-family: "Roboto-Regular", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 26px; color: #333; text-align: center; }

.contact__icon { min-height: 53px; margin-bottom: 15px; }

.contact__text { color: inherit; text-decoration: none; }

.footer { padding: 24px 0; text-align: center; color: rgba(0, 0, 0, 0.5); font-family: "Roboto-Regular", Helvetica, Arial, sans-serif; font-weight: normal; font-size: 11px; letter-spacing: 0.025em; text-transform: uppercase; background: #EDF0F7; }


